import React, { useState, useContext } from 'react';
//import OwlCarousel from 'react-owl-carousel';
// import 'owl.carousel/dist/assets/owl.carousel.css';
// import 'owl.carousel/dist/assets/owl.theme.default.css';
import ReactPaginate from 'react-paginate';
import { Coviddd } from './Covid19';
function Worldcovid(props) {
    let covidd = useContext(Coviddd);
    let [pageNumber, setPageno] = useState(0);
    let country = covidd.slice(0, covidd.length);
    let countryperpage = 4;
    let pagevisited = pageNumber * countryperpage;
    let displayCountry = country.slice(pagevisited, pagevisited + countryperpage).map((curval) => {
        return (
            <>
                <div className="col-lg-3 col-md-4 col-12">
                    <div className={`card shadow-sm countryy mb-3 ${!props.theme ? '' : 'cardd'}`}>
                        <div className="card-body">
                            <h4 className="card-title ">{curval.country}</h4>
                            <div className="row">
                                <div className="col-lg-12 col-md-12 col-12">
                                    <div className="alert alert-info m-3" role="alert">
                                        <span>Total Cases:</span><h5 className="font-weight-bold fontt">{curval.cases}</h5>
                                    </div>
                                </div>
                                <div className="col-lg-12 col-md-12 col-12">
                                    <div className="alert alert-success m-3" role="alert">
                                        <span>Total Recovered:</span><h5 className="font-weight-bold fontt">{curval.recovered}</h5>
                                    </div>
                                </div>
                                <div className="col-lg-12 col-md-12 col-12">
                                    <div className="alert alert-danger m-3" role="alert">
                                        <span>Total Deaths:</span><h5 className="font-weight-bold fontt">{curval.deaths}</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )
    });
    let pageCount = Math.ceil(country.length / countryperpage);
    let changePage = ({ selected }) => {
        setPageno(selected);
    }
    // const options = {
    //     responsiveClass: true,
    //     margin: 8,
    //     nav: true,
    //     dots: true,
    //     autoplay: true,
    //     smartSpeed: 1000,
    //     autoplayHoverPause: true,
    //     responsive: {
    //         0: {
    //             items: 1,
    //         },
    //         600: { items: 1 },
    //         700: {
    //             items: 2,
    //         },
    //         1000: {
    //             items: 4,

    //         }
    //     },
    // };
    return (
        <>
            <h3 className="text-center">Country Data:</h3>
            <hr className="mx-auto w-25" />
            <div className="row">
                {/* <OwlCarousel className='owl-theme'{...options}>  </OwlCarousel> */}
                {
                    //console.log(country)
                    displayCountry

                }
                <ReactPaginate
                    previousLabel="Previous"
                    nextLabel="Next"
                    pageCount={pageCount}
                    onPageChange={changePage}
                    containerClassName={"paginationbtn"}
                    previousLinkClassName={"previousbtn"}
                    nextLinkClassName={"nextbtn"}
                    disabledClassName={"paginaiondisabled"}
                    activeClassName={"paginationactive"}
                />
            </div>
        </>
    )
}
export default Worldcovid
