import React, { useContext } from 'react';
import Covidjson from './Covidjson';
import Logo from './logo.png';
import { Themee } from './Theme';
let Covid = () => {
    let [check, setCheck] = useContext(Themee);
    var d = new Date();
    var n = d.getFullYear();
    let Theme = () => {
        if (check) {
            setCheck(false);
            document.body.style.backgroundColor = "white";
            document.body.style.color = "black";
            //console.log('white')
        }
        else {
            setCheck(true);
            document.body.style.backgroundColor = "black";
            document.body.style.color = "white";
            //console.log('black');
        }
    }
    return (
        <>
            <div className="container">
                <h3 className="text-center mt-3">Coronavirus (COVID-19) Tracker </h3>
                <button className={` btn btn-outline-info ml-2 theme mt-2  ${!check ? '' : 'text-white'}`} onClick={Theme}>{!check ? 'Change Theme[Light]' : 'Change Theme[Black]'}</button>
                <hr className="w-25 mx-auto" />
                <div className="row">
                    <div className="col-md-12 col-lg-12 col-12">
                        <Covidjson />
                    </div>
                </div>
            </div>
            <footer className="page-footer font-small">
                <div className="footer-copyright text-center py-3 text-white">Â© {n} Developed by:
                    <img
                        src={Logo}
                        width="60"
                        height="38"
                        className="d-inline-block align-top"
                        alt="logo"
                    /><a href="#" className="text-warning"> Ashish Thapa</a>
                </div>
            </footer>
        </>
    )
}
export default Covid;