import axios from 'axios';
import React, { createContext } from 'react';
import { useQuery } from 'react-query';
import Loadingg from './Loading';
import Servererror from './servererror.gif';
export let Coviddd = createContext();
let Pandemic = (props) => {
    let Countrydata = () => {
        let url = 'https://disease.sh/v3/covid-19/countries';
        return axios.get(url).then(response => response.data)
    }
    let { data, isLoading, isError } = useQuery('country-data', Countrydata)
    if (isError) {
        return (<><div className='container'><img className='img-fluid ' src={Servererror} alt='servererror' /></div></>)
    }
    if (isLoading) {
        return (<Loadingg className='text-center mt-5' />)
    }
    console.log(data);
    return (
        <>
            <Coviddd.Provider value={data}>
                {props.children}
            </Coviddd.Provider>
        </>
    )
}
export default Pandemic;