import React, { useState, createContext } from 'react'
export let Themee = createContext();
export default function Theme(props) {
    let [check, setCheck] = useState('');
    console.log(check);
    return (
        <div>
            <Themee.Provider value={[check, setCheck]}>
                {props.children}
            </Themee.Provider>
        </div>
    )
}
