import React, { useState, useContext } from 'react';
import Totalworldwidecase from './Totalcovidworldwidecase';
import axios from 'axios';
import { useQuery } from 'react-query'
import Worldwidecovid from './Worldcovid';
import Countrycase from './Countrycases';
import { Coviddd } from './Covid19';
import { Themee } from './Theme';
import Servererror from './servererror.gif'
let Covidjson = () => {
    let covidd = useContext(Coviddd);
    let [countrydata, setCountrydata] = useState({});
    let [select, setSelect] = useState(true);
    let [loading, setLoading] = useState(false);
    let [check, setCheck] = useContext(Themee);
    let Worldwide = () => {
        let url = 'https://disease.sh/v3/covid-19/all'
        return axios.get(url).then(response => response.data)
    }
    let { data, isLoading, isError } = useQuery('world-data', Worldwide)
    if (isError) {
        return <><div className='container'><img className='img-fluid' src={Servererror} alt='servererr' /></div></>
    }
    console.log(covidd);
    let Countrychoose = async (e) => {
        const countrycode = e.target.value;
        setSelect(false);
        setLoading(false);
        //console.log(countrycode);
        if (countrycode === "worldwide") {
            setSelect(true);
        }
        else {
            try {
                let url = `https://disease.sh/v3/covid-19/countries/${countrycode}`
                axios.get(url).then((response) => {
                    setCountrydata(response.data)
                    setLoading(true);
                })
            }
            catch (e) {
                alert('Took long time: Refresh page:)')
            }
        }
    }
    return (
        <>
            <Totalworldwidecase theme={check} load={isLoading} countryInfo={data} />
            <select className={!check ? 'form-select mt-4 mb-2 p-2 shadow rounded-pill' : 'rounded-pill form-select shadow p-2 mt-4 bg-dark text-white mb-2'} aria-label="Default select example" onChange={Countrychoose}>
                <option selected value="worldwide">Select Country</option>
                {covidd.map((curvalue, indexx) => {
                    return (<> <option value={curvalue.countryInfo.iso2} key={indexx}>{curvalue.country}</option></>);
                })}
            </select>
            {select ? (<>
                <div className='alert-info shadow-sm mb-3 rounded-pill p-1 text-center font-weight-bold mt-3'><h5 className='mt-2'>Choose Country:</h5></div>
            </>) : (<Countrycase theme={check} countryData={countrydata} load={loading} />)}
            {/* <Worldwidecovid theme={check} /> */}
        </>
    )
}
export default Covidjson;